<template>
  <div>

    <v-row class="px-6 pt-6 pb-2">
      <v-col :cols="isMobile ? 12 : 4"><h2>Member Registration</h2>
      <small v-if="member.reference">Ref. Number#: {{ member.reference }}</small></v-col>
      <v-col :cols="isMobile ? 12 : 4">
        <div style="display: flex;justify-content: center;">
        <v-avatar size="100" >
      <img
     v-if="member.image"
        :src="member.image"
        :alt="member.name"
      >
      <img
     v-else
        src="images/no-photo.jpeg"
        :alt="member.name"
      >
    </v-avatar></div></v-col>
      <v-col :cols="isMobile ? 12 : 4"><v-text-field 
        label="Search By Ref. Number eg. GhSA3247216" 
        v-model="searchPass"
        class="mt-n2" 
        @blur="getMemberByPass()"></v-text-field></v-col>
    </v-row>

    <!-- alert message -->
    <v-alert outlined  class="mx-6 my-4" v-if="showAlert"
      :type="alertType"
    >{{ alertMsg }}
    </v-alert>


   <!-- Member registration form -->
   <v-row class="px-6 pt-2 pb-6">

    <v-col :cols="isMobile ? 12 : 4">
    <v-text-field label="Surname" v-model="member.surname" prepend-inner-icon="mdi-account" filled outlined></v-text-field>
    </v-col>


    <v-col :cols="isMobile ? 12 : 4">
    <v-text-field label="Full Names" v-model="member.name" prepend-inner-icon="mdi-account" filled outlined></v-text-field>
    </v-col>

    <v-col :cols="isMobile ? 12 : 4">
    <v-text-field label="Mobile Number" v-model="member.phone" prepend-inner-icon="mdi-cellphone-basic" filled outlined></v-text-field>
    </v-col>

    <v-col :cols="isMobile ? 12 : 4">
    <v-text-field label="Email Address" v-model="member.email" prepend-inner-icon="mdi-email" filled outlined></v-text-field>
    </v-col>

    <v-col :cols="isMobile ? 12 : 4">
    <v-text-field label="Passport Number" v-model="member.passport" prepend-inner-icon="mdi-numeric" filled outlined></v-text-field>
    </v-col>

    <v-col :cols="isMobile ? 12 : 4">
      <v-menu
        ref="menu"
        v-model="menu"
        :close-on-content-click="false"
        :return-value.sync="member.dob"
        transition="scale-transition"
        offset-y
        min-width="auto"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-text-field
          filled 
          outlined
            v-model="member.dob"
            label="Date of Birth"
            prepend-inner-icon="mdi-calendar"
            readonly
            v-bind="attrs"
            v-on="on"
          ></v-text-field>
        </template>
        <v-date-picker
          v-model="member.dob"
          no-title
          scrollable
        >
          <v-spacer></v-spacer>
          <v-btn
            text
            color="primary"
            @click="menu = false"
          >
            Cancel
          </v-btn>
          <v-btn
            text
            color="primary"
            @click="$refs.menu.save(member.dob)"
          >
            OK
          </v-btn>
        </v-date-picker>
      </v-menu>
    
    </v-col>

    <v-col :cols="12">
    <v-textarea :rows="4" label="Physical Address" prepend-inner-icon="mdi-map-marker" v-model="member.address" filled outlined></v-textarea>
    </v-col>

    <v-col :cols="isMobile ? 12 : 3">
     
     <v-autocomplete prepend-inner-icon="mdi-gender-male-female" :items="['Male','Female']" label="Gender" v-model="member.gender" filled outlined></v-autocomplete>
     </v-col>

    <v-col :cols="isMobile ? 12 : 3">
    <v-text-field label="City" v-model="member.city" prepend-inner-icon="mdi-home-city" filled outlined></v-text-field>
    </v-col>

    <v-col :cols="isMobile ? 12 : 3">
     
    <v-autocomplete prepend-inner-icon="mdi-sitemap" :items="['Eastern Cape','Free State','Gauteng',
    'KwaZulu-Natal'
    ,'Limpopo','Mpumalanga','Northern Cape','North West', 'Western Cape']" label="Province" v-model="member.province" filled outlined></v-autocomplete>
    </v-col>

    <v-col :cols="isMobile ? 12 : 3" ><Camera @picture-taken="getImageUrl"></Camera></v-col>

  
    <v-col :cols="isMobile ? 12 : 4" >
    <v-btn block :style="{'background-color':getPrimaryColor}" dark x-large @click="submitBtnClicked()"><v-icon>mdi-upload</v-icon>{{submitBtnLabel}}</v-btn>
    </v-col>
   </v-row>
   
  </div>
</template>

<script>
import Camera from "@/components/Camera.vue"

export default {
  name: "HomeView",
  components:{
    Camera
  },
  computed:{
    isMobile(){
      return this.$store.getters.isMobile
    },
    getApiUrl(){
      return this.$store.getters.getApiUrl
    },
    getBackgroundColor(){
      return this.$store.getters.getBackgroundColor
    },
    getPrimaryColor(){
      return this.$store.getters.getPrimaryColor
    },
    getSecondaryColor(){
      return this.$store.getters.getSecondaryColor
    },
    getTertiaryColor(){
      return this.$store.getters.getTertiaryColor
    },
  },
  mounted(){
    this.$store.commit("setDeviceType",this.$vuetify.breakpoint.smAndDown)
  },
  updated(){
    this.$store.commit("setDeviceType",this.$vuetify.breakpoint.smAndDown)
  },
  data:()=>({
    date: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
    searchPass:null,
    menu:false,
    alertMsg:"Member Data Captured Successfully!",
    showAlert:false,
    alertType:"info",
    submitBtnLabel:"Submit Registration Data",
    member:{
      id:null,
      surname:"",
      reference:null,
      name:"",
      passport:"",
      dob:(new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
      gender:"",
      phone:"",
      email:"",
      address:"",
      city:"",
      province:"",
      image:""
    }
  }),
  methods:{
    getImageUrl(imageBase64String){
      this.member.image = imageBase64String
    },
    submitBtnClicked(){
     this.$store.commit("setLoader",true)
      fetch(this.getApiUrl+"/save-or-update-member-info",{
        method:"POST",
        headers:{
          "Content-Type" : "Application/json"
        },
        body:JSON.stringify(this.member)
      }).then(res => res.json()).then(res => {
        this.$store.commit("setLoader",false)
        if(res && res.status == "success"){
          this.$toast.success("Data Submitted Successfully!")
          this.alertType = "success"
          this.alertMsg = "Data Submitted Successfully!, Your Ref. Number is: " + res.member.reference + ". An SMS with your Ref Number has been sent to your phone number."
          this.showAlert = true
        }else{
          this.$toast.error("Oops, Something Went Wrong!")
          this.alertType = "error"
          this.alertMsg = "Oops, Something Went Wrong!"
          this.showAlert = true
        }
      })
    },
    getMemberByPass(){
      if(!this.searchPass){
        return false;
      }
      this.$store.commit("setLoader",true)
      fetch(this.getApiUrl+"/get-a-member-info",{
        method:"POST",
        headers:{
          "Content-Type" : "Application/json"
        },
        body:JSON.stringify({reference:this.searchPass})
      }).then(res => res.json()).then(res => {
        this.$store.commit("setLoader",false)
        if(res && res.status == "success"){
          this.showAlert = false
          this.member = res.member
        }else{
          this.$toast.error("Oops, No Member with reference " +this.searchPass + " Found!")
          this.alertType = "error"
          this.alertMsg = "Oops, No Member with reference " +this.searchPass + " Found!"
          this.showAlert = true
        }
      })
    }
  }


};
</script>
