import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    apiUrl:"https://api.baronitsolutions.co.za",
    backgroundColor:"#F5F7FA",
    primaryColor:"#1B5E20",
    secondaryColor:"#B71C1C",
    tertiaryColor:"black",
    mobileDevice:false,
    authenticated: JSON.parse(localStorage.getItem('authenticated')) || false,
    loader:false,
  },
  getters: {
    isMobile(state){
      return state.mobileDevice
    },
    getApiUrl(state){
      return state.apiUrl
    },
    getAuthenticated(state){
      if(JSON.parse(localStorage.getItem('authenticated'))){
        state.authenticated = JSON.parse(localStorage.getItem('authenticated'))
      }
      return state.authenticated
    },
    getLoader(state){
      return state.loader
    },
    getBackgroundColor(state){
      if(localStorage.getItem('backgroundColor')){
        state.backgroundColor = localStorage.getItem('backgroundColor')
      }
      return state.backgroundColor
    },
    getPrimaryColor(state){
      if(localStorage.getItem('primaryColor')){
        state.primaryColor = localStorage.getItem('primaryColor')
      }
      return state.primaryColor
    },
    getSecondaryColor(state){
      if(localStorage.getItem('secondaryColor')){
        state.secondaryColor = localStorage.getItem('secondaryColor')
      }
      return state.secondaryColor
    },
    getTertiaryColor(state){
      if(localStorage.getItem('tertiaryColor')){
        state.tertiaryColor = localStorage.getItem('tertiaryColor')
      }
      return state.tertiaryColor
    },
  },
  mutations: {
    setAuthenticated(state, payload){
      state.authenticated = payload
      localStorage.setItem("authenticated",JSON.stringify(payload))
    },
    setDeviceType(state,payload){
      state.mobileDevice = payload
    },
    setLoader(state,payload){
      state.loader = payload
    }
  },
  actions: {},
  modules: {},
});
