<template>
  <v-dialog v-model="dialog" max-width="600px">
    <template v-slot:activator="{ on, attrs }">
      <v-btn x-large block color="primary" style="border-radius:10px"  dark v-bind="attrs" v-on="on" @click="startCamera">
        <v-icon>mdi-camera</v-icon> Take Selfie
      </v-btn>
    </template>
    <v-card>
      <v-card-title>
        <span class="text-h5">Capture Photo</span>
      </v-card-title>
      <v-card-text>
        <div>
          <!-- Video element to display the live stream -->
          <video ref="video" autoplay playsinline style="width: 100%;" v-if="showVideo"></video>
        </div>
        <div>
          <!-- Canvas element to draw the captured image -->
          <canvas ref="canvas" style="display: none;"></canvas>
        </div>
        <!-- Display captured image preview -->
        <v-img v-if="imgSrc" :src="imgSrc" aspect-ratio="1.7" class="mt-4"></v-img>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="blue darken-1" text @click="closeCamera">Close</v-btn>
        <v-btn color="blue darken-1" text @click="takeSnapshot">Take Photo</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
     computed:{
      getApiUrl(){
        return this.$store.getters.getApiUrl
      },
      isMobile(){
        return this.$vuetify.breakpoint.smAndDown
      }
    },
  data() {
    return {
      showVideo:false,
      dialog: false,
      videoStream: null,
      imgSrc: null,
    };
  },
  methods: {
    async startCamera() {
      this.showVideo = true
      // Access the user's camera using the WebRTC API
      try {
        const stream = await navigator.mediaDevices.getUserMedia({ video: true });
        this.videoStream = stream;
        // Attach the stream to the video element
        this.$refs.video.srcObject = stream;
      } catch (error) {
        console.error("Error accessing camera:", error);
        alert("Could not access the camera. Please check permissions.");
      }
    },
    takeSnapshot() {
      
      const video = this.$refs.video;
      const canvas = this.$refs.canvas;
      const context = canvas.getContext('2d');

      // Set canvas dimensions to match the video feed
      canvas.width = video.videoWidth;
      canvas.height = video.videoHeight;

      // Draw the current video frame onto the canvas
      context.drawImage(video, 0, 0, canvas.width, canvas.height);

      // Get the image data as a Data URL (base64)
      this.imgSrc = canvas.toDataURL('image/png');
      
      // You can now use `this.imgSrc` (e.g., upload it to a server)
      this.showVideo = false
      this.$emit("picture-taken", this.imgSrc);
    },
    closeCamera() {
      // Stop all video tracks to turn off the webcam indicator/light
      if (this.videoStream) {
        this.videoStream.getTracks().forEach(track => track.stop());
      }
      this.showVideo = false
      this.videoStream = null;
      this.dialog = false;
      this.imgSrc = null; // Clear the preview when closing
    }
  },
  // Ensure camera stops if the dialog is closed via other means (e.g., clicking outside)
  watch: {
    dialog(val) {
      if (!val) {
        this.closeCamera();
      }
    }
  }
};
</script>
