<template>
  <v-app :style="{ backgroundColor: getBackgroundColor }">
    <v-app-bar app :color="getPrimaryColor" dark>
      <div class="d-flex align-center">
        <v-img
          alt="Ghana Coat of Arms"
          class="shrink mr-2"
          contain
          src="images/coat-of-arms.jpg"
          transition="scale-transition"
          width="60"
          height="60"
          style="border-radius: 50%;"
        />

       <h1 >GhanaSA</h1>
      </div>

      <v-spacer></v-spacer>

      <v-btn
      v-if="!getAuthenticated"
      @click="goToLogin()"
        text
      >
        <span class="mr-2">Admin Login</span>
        <v-icon>mdi-account-lock</v-icon>
      </v-btn>

      <div v-else>
        <v-btn text v-if="pathname && !pathname.includes('admin')" @click="goToAdmin()">Admin Dash</v-btn>
        <v-btn
      
      @click="logout()"
        text
      >
        <span class="mr-2">Logout</span>
        <v-icon>mdi-logout</v-icon>
      </v-btn>
      </div>
     
    </v-app-bar>
    

    <v-main>
      <router-view />
    </v-main>

    <Loader></Loader>
  </v-app>
</template>

<script>
import Loader from "@/components/Loader.vue"

export default {
  name: "App",
  components:{
    Loader
  },
  computed:{
    getBackgroundColor(){
      return this.$store.getters.getBackgroundColor
    },
    getPrimaryColor(){
      return this.$store.getters.getPrimaryColor
    },
    getSecondaryColor(){
      return this.$store.getters.getSecondaryColor
    },
    getTertiaryColor(){
      return this.$store.getters.getTertiaryColor
    },
    getAuthenticated(){
      return this.$store.getters.getAuthenticated
    }
  },
  mounted(){
   this.pathname = location.pathname
    this.$store.commit("setDeviceType",this.$vuetify.breakpoint.smAndDown)
  },
  updated(){
    this.pathname = location.pathname
    this.$store.commit("setDeviceType",this.$vuetify.breakpoint.smAndDown)
  },

  data: () => ({
    pathname:""
  }),
  methods:{
    goToLogin(){
      if(location.pathname.includes("login")){
        this.$toast.info("You are already here!")
        return false;
      }
      this.$router.push({path:"/login"})
    },
    goToAdmin(){
      this.$router.push({path:"/admin-dashboard"})
    },
    logout(){
      this.$store.commit("setAuthenticated",false)
      this.$toast.success("You have logged out successfully!")
      this.$router.push({path:"/"})
    }
  }
};
</script>
